.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;
}

.dialogsItems {
    padding: 10px;
    color: white;
}

.dialogsItems .active{
    color: gold;
}
.messages {
    padding: 10px;
}

.dialog img{
    width: 50px;
    height: 50px;
    border-radius: 30px;
}
.dialogsDev{
    text-align:center;
    font-size: 54px;
}